[class*='kss-modifier__wrapper'] {
  background-color: #041f17;
  color: #12e09f; }
  [class*='kss-modifier__wrapper'] .kss-style {
    color: inherit; }

[id*='wrappers'] .w {
  box-shadow: 0 0 0 1px #12e09f inset;
  margin-bottom: calc(0.75 * 1rem);
  margin-top: calc(0.75 * 1rem); }

[id*='spacing'] .mt,
[id*='spacing'] .pt {
  box-shadow: 0 0 0 1px #12e09f inset;
  padding-left: calc(0.75 * 1rem);
  padding-right: calc(0.75 * 1rem); }
